import Head from '../components/Head';
import Navbar from '../components/Navbar';
import BigVideo from '../components/BigVideo';
import { ParallaxProvider } from 'react-scroll-parallax';
import About from '../components/About';
export default () => {
    return (
        <ParallaxProvider>
            <style global jsx>{`
                body {
                    margin: 0;
                    background:#eee;
                }
             `}</style>
            <Head/>
            <Navbar />
            <BigVideo />
            <About />
        </ParallaxProvider>);
}
