import * as styleConstants from  '../constants/StyleConstants';
export default class Section {
    render(){
        return <div className="section-container">
        <style jsx>{`
            .section-container{
                background:#fff;
                width:100%;
                ${styleConstants.containerPadding}
            }
        `}</style>
        {this.props.children}
    </div>
    }
}